import uniRate from '../../components/uni-rate/uni-rate.vue'
export default{
	components: {
		uniRate
	},
	data(){
		return{
			windowHeight: '',
			infor:'',
			id:'',
			tg:false,
			focusing:false,
			order_id:'',
			reply:'',
		}
	},
	onLoad() {
		var that = this;
		that.windowHeight = this.statusBarHeight + 44;
		uni.getStorage({
			key: 'userinfo',
			success: function(res) {
				that.id = res.data.id;
				that.getinfo();
			}
		})
	},
	methods:{
		getinfo:function(){
			let that=this;
			uni.showLoading({
				
			})
			uni.request({
				url: this.serveipd+'/api/merchant/comment/comments',
				method: 'GET',
				data: {
					merchantId:that.id
				},
				success: res => {
					uni.hideLoading()
					that.infor=res.data.data.datas;
					console.log(that.infor);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		onChange(e) {
			console.log('rate发生改变:' + JSON.stringify(e));
		},
		answer:function(id){
			let that=this;
			that.tg=true;
			that.focusing=true;
			that.order_id=id;
		},
		complete:function(){
			let that=this;
			uni.request({
				url:that.serveipd+"/api/merchant/comment/comment/"+that.order_id+"/reply",
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				method:"POST",
				data:{
					reply:that.reply
				},
				success(res) {
					that.tg=false;
					that.getinfo();
				}
			})
		},
		back: function() {
			uni.navigateBack({
				delta: 1
			});
		},
	}
}
